import React, { useState, useEffect } from 'react';
import { 
  Button, 
  Badge, 
  Modal, 
  TextInput, 
  Label, 
  Select, 
  FileInput,
  Checkbox,
  Dropdown,
  Pagination
} from 'flowbite-react';
import IconContainer from '../components/common/IconContainer';
import { 
  PlusIcon, 
  PencilSquareIcon as PencilIcon, 
  EyeIcon,
  EllipsisVerticalIcon,
  ArrowDownTrayIcon,
  FunnelIcon
} from '@heroicons/react/24/outline';
import { tiposEPI } from '../mocks/data';
import { TipoEPI } from '../types';
import { getCategoryIcon } from '../utils/epiIcons';

const CatalogoEPIs: React.FC = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('');
  const [showModal, setShowModal] = useState(false);
  const [modalMode, setModalMode] = useState<'create' | 'edit' | 'view'>('create');
  const [selectedEPI, setSelectedEPI] = useState<TipoEPI | null>(null);
  const [filteredEPIs, setFilteredEPIs] = useState<TipoEPI[]>(tiposEPI);
  const [selectedEPIs, setSelectedEPIs] = useState<string[]>([]);

  const categories = Array.from(new Set(tiposEPI.map(epi => epi.categoria)));

  useEffect(() => {
    const filtered = tiposEPI.filter(epi => {
      const matchesSearch = epi.nomeEquipamento.toLowerCase().includes(searchTerm.toLowerCase()) ||
                          epi.numeroCA.toLowerCase().includes(searchTerm.toLowerCase());
      const matchesCategory = !selectedCategory || epi.categoria === selectedCategory;
      return matchesSearch && matchesCategory;
    });
    setFilteredEPIs(filtered);
  }, [searchTerm, selectedCategory]);

  const handleOpenModal = (mode: 'create' | 'edit' | 'view', epi?: TipoEPI) => {
    setModalMode(mode);
    setSelectedEPI(epi || null);
    setShowModal(true);
  };

  const handleCloseModal = () => {
    setShowModal(false);
    setSelectedEPI(null);
  };

  const handleSelectAll = (checked: boolean) => {
    if (checked) {
      setSelectedEPIs(filteredEPIs.map(epi => epi.id));
    } else {
      setSelectedEPIs([]);
    }
  };

  const handleSelectEPI = (epiId: string, checked: boolean) => {
    if (checked) {
      setSelectedEPIs([...selectedEPIs, epiId]);
    } else {
      setSelectedEPIs(selectedEPIs.filter(id => id !== epiId));
    }
  };

  return (
    <div className="bg-white dark:bg-gray-900">
      <div className="mx-auto max-w-full">
        {/* Header */}
        <div className="bg-white dark:bg-gray-900 relative shadow-md sm:rounded-lg overflow-hidden">
          <div className="flex flex-col md:flex-row items-center justify-between space-y-3 md:space-y-0 md:space-x-4 p-4">
            <div className="w-full md:w-1/2">
              <h1 className="text-lg font-semibold text-gray-900 dark:text-white">Catálogo de EPIs</h1>
              <p className="text-gray-500 dark:text-gray-400">Gerencie os tipos de Equipamentos de Proteção Individual</p>
            </div>
            <div className="w-full md:w-auto flex flex-col md:flex-row space-y-2 md:space-y-0 items-stretch md:items-center justify-end md:space-x-3 flex-shrink-0">
              <Button onClick={() => handleOpenModal('create')} color="blue" sizing="sm" className="rounded-sm">
                <PlusIcon className="w-4 h-4 mr-2" />
                Novo EPI
              </Button>
              <Dropdown
                label=""
                dismissOnClick={false}
                renderTrigger={() => (
                  <Button color="light" sizing="sm" className="rounded-sm">
                    <EllipsisVerticalIcon className="w-4 h-4" />
                  </Button>
                )}
              >
                <Dropdown.Item icon={ArrowDownTrayIcon}>
                  Exportar
                </Dropdown.Item>
                <Dropdown.Item icon={PlusIcon}>
                  Importar
                </Dropdown.Item>
              </Dropdown>
            </div>
          </div>

          {/* Search and Filters */}
          <div className="flex flex-col md:flex-row items-center justify-between space-y-3 md:space-y-0 md:space-x-4 p-4 border-t dark:border-gray-700">
            <div className="w-full md:w-1/3">
              <Label htmlFor="table-search" className="sr-only">Buscar</Label>
              <div className="relative">
                <div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none">
                  <svg className="w-4 h-4 text-gray-500 dark:text-gray-400" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z" clipRule="evenodd"></path>
                  </svg>
                </div>
                <TextInput
                  id="table-search"
                  placeholder="Buscar EPIs..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  sizing="sm"
                  className="pl-10 rounded-sm"
                />
              </div>
            </div>
            <div className="w-full md:w-auto flex flex-col md:flex-row space-y-2 md:space-y-0 items-stretch md:items-center justify-end md:space-x-3 flex-shrink-0">
              <Select value={selectedCategory} onChange={(e) => setSelectedCategory(e.target.value)} sizing="sm" className="rounded-sm">
                <option value="">Todas as categorias</option>
                {categories.map(category => (
                  <option key={category} value={category}>{category}</option>
                ))}
              </Select>
              <Button color="light" sizing="sm" className="rounded-sm">
                <FunnelIcon className="w-4 h-4 mr-2" />
                Filtros
              </Button>
            </div>
          </div>

          {/* Table */}
          <div className="overflow-x-auto">
            <table className="w-full text-sm text-left text-gray-500 dark:text-gray-400">
              <thead className="text-xs text-gray-700 uppercase bg-gray-50 dark:bg-gray-700 dark:text-gray-400">
                <tr>
                  <th scope="col" className="p-4">
                    <div className="flex items-center">
                      <Checkbox
                        checked={selectedEPIs.length === filteredEPIs.length && filteredEPIs.length > 0}
                        onChange={(e) => handleSelectAll(e.target.checked)}
                      />
                      <Label htmlFor="checkbox-all" className="sr-only">checkbox</Label>
                    </div>
                  </th>
                  <th scope="col" className="px-4 py-3">Foto</th>
                  <th scope="col" className="px-4 py-3">CA</th>
                  <th scope="col" className="px-4 py-3">Equipamento</th>
                  <th scope="col" className="px-4 py-3">Fabricante</th>
                  <th scope="col" className="px-4 py-3">Categoria</th>
                  <th scope="col" className="px-4 py-3">Vida Útil</th>
                  <th scope="col" className="px-4 py-3">
                    <span className="sr-only">Ações</span>
                  </th>
                </tr>
              </thead>
              <tbody>
                {filteredEPIs.map((epi) => (
                  <tr key={epi.id} className="border-b dark:border-gray-700 hover:bg-gray-50 dark:hover:bg-gray-600">
                    <td className="w-4 p-4">
                      <div className="flex items-center">
                        <Checkbox
                          checked={selectedEPIs.includes(epi.id)}
                          onChange={(e) => handleSelectEPI(epi.id, e.target.checked)}
                        />
                        <Label htmlFor={`checkbox-${epi.id}`} className="sr-only">checkbox</Label>
                      </div>
                    </td>
                    <td className="px-4 py-4 whitespace-nowrap">
                      <IconContainer 
                        icon={getCategoryIcon(epi.categoria, "w-6 h-6")}
                        color="gray"
                        size="lg"
                      />
                    </td>
                    <td className="px-4 py-4 whitespace-nowrap font-medium text-gray-900 dark:text-white">
                      {epi.numeroCA}
                    </td>
                    <th scope="row" className="px-4 py-4 text-gray-900 whitespace-nowrap dark:text-white">
                      <div className="text-base font-semibold">{epi.nomeEquipamento}</div>
                    </th>
                    <td className="px-4 py-4 whitespace-nowrap text-gray-900 dark:text-white">
                      {epi.fabricante}
                    </td>
                    <td className="px-4 py-4 whitespace-nowrap">
                      <Badge color="gray">{epi.categoria}</Badge>
                    </td>
                    <td className="px-4 py-4 whitespace-nowrap text-gray-900 dark:text-white">
                      {epi.vidaUtilDias} dias
                    </td>
                    <td className="px-4 py-4 flex items-center justify-end">
                      <Button
                        sizing="sm"
                        color="blue"
                        onClick={() => handleOpenModal('view', epi)}
                        className="mr-2 rounded-sm"
                      >
                        <EyeIcon className="w-4 h-4" />
                      </Button>
                      <Button
                        sizing="sm"
                        color="light"
                        onClick={() => handleOpenModal('edit', epi)}
                        className="mr-2 rounded-sm"
                      >
                        <PencilIcon className="w-4 h-4" />
                      </Button>
                      <Dropdown
                        label=""
                        dismissOnClick={false}
                        renderTrigger={() => (
                          <Button sizing="sm" color="light" className="rounded-sm">
                            <EllipsisVerticalIcon className="w-4 h-4" />
                          </Button>
                        )}
                      >
                        <Dropdown.Item icon={PlusIcon}>
                          Duplicar
                        </Dropdown.Item>
                        <Dropdown.Item icon={ArrowDownTrayIcon}>
                          Exportar
                        </Dropdown.Item>
                      </Dropdown>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          {/* Pagination */}
          <div className="flex flex-col md:flex-row justify-between items-start md:items-center space-y-3 md:space-y-0 p-4">
            <span className="text-sm font-normal text-gray-500 dark:text-gray-400">
              Mostrando <span className="font-semibold text-gray-900 dark:text-white">1-{filteredEPIs.length}</span> de <span className="font-semibold text-gray-900 dark:text-white">{filteredEPIs.length}</span>
            </span>
            <Pagination
              currentPage={1}
              totalPages={1}
              onPageChange={() => {}}
              showIcons
            />
          </div>
        </div>
      </div>

      {/* Modal de Cadastro/Edição/Visualização */}
      <Modal show={showModal} onClose={handleCloseModal} size="2xl">
        <div className="p-6">
          <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-4">
            {modalMode === 'create' && 'Novo EPI'}
            {modalMode === 'edit' && 'Editar EPI'}
            {modalMode === 'view' && 'Detalhes do EPI'}
          </h3>
          <div className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <Label htmlFor="numeroCA" className="text-gray-700 dark:text-gray-300">Número do CA</Label>
                <TextInput
                  id="numeroCA"
                  value={selectedEPI?.numeroCA || ''}
                  onChange={(e) => selectedEPI && setSelectedEPI({...selectedEPI, numeroCA: e.target.value})}
                  disabled={modalMode === 'view'}
                  sizing="sm"
                  className="rounded-sm"
                />
              </div>
              <div>
                <Label htmlFor="nomeEquipamento" className="text-gray-700 dark:text-gray-300">Nome do Equipamento</Label>
                <TextInput
                  id="nomeEquipamento"
                  value={selectedEPI?.nomeEquipamento || ''}
                  onChange={(e) => selectedEPI && setSelectedEPI({...selectedEPI, nomeEquipamento: e.target.value})}
                  disabled={modalMode === 'view'}
                  sizing="sm"
                  className="rounded-sm"
                />
              </div>
              <div>
                <Label htmlFor="fabricante" className="text-gray-700 dark:text-gray-300">Fabricante</Label>
                <TextInput
                  id="fabricante"
                  value={selectedEPI?.fabricante || ''}
                  onChange={(e) => selectedEPI && setSelectedEPI({...selectedEPI, fabricante: e.target.value})}
                  disabled={modalMode === 'view'}
                  sizing="sm"
                  className="rounded-sm"
                />
              </div>
              <div>
                <Label htmlFor="categoria" className="text-gray-700 dark:text-gray-300">Categoria</Label>
                <Select
                  id="categoria"
                  value={selectedEPI?.categoria || ''}
                  onChange={(e) => selectedEPI && setSelectedEPI({...selectedEPI, categoria: e.target.value})}
                  disabled={modalMode === 'view'}
                  sizing="sm"
                  className="rounded-sm"
                >
                  <option value="">Selecione uma categoria</option>
                  {categories.map((cat) => (
                    <option key={cat} value={cat}>{cat}</option>
                  ))}
                </Select>
              </div>
              <div>
                <Label htmlFor="vidaUtilDias" className="text-gray-700 dark:text-gray-300">Vida Útil (dias)</Label>
                <TextInput
                  id="vidaUtilDias"
                  type="number"
                  value={selectedEPI?.vidaUtilDias || ''}
                  onChange={(e) => selectedEPI && setSelectedEPI({...selectedEPI, vidaUtilDias: parseInt(e.target.value) || 0})}
                  disabled={modalMode === 'view'}
                  sizing="sm"
                  className="rounded-sm"
                />
              </div>
              <div>
                <Label htmlFor="foto" className="text-gray-700 dark:text-gray-300">Foto do EPI</Label>
                <FileInput
                  id="foto"
                  accept="image/*"
                  onChange={(e) => {
                    if (e.target.files && e.target.files[0] && selectedEPI) {
                      const reader = new FileReader();
                      reader.onload = (event) => {
                        setSelectedEPI({
                          ...selectedEPI,
                          foto: event.target?.result as string
                        });
                      };
                      reader.readAsDataURL(e.target.files[0]);
                    }
                  }}
                  disabled={modalMode === 'view'}
                  sizing="sm"
                  className="rounded-sm"
                />
              </div>
            </div>
            
            {selectedEPI?.foto && (
              <div className="mt-4">
                <Label className="text-gray-700 dark:text-gray-300">Pré-visualização</Label>
                <div className="mt-2">
                  <img 
                    src={selectedEPI.foto} 
                    alt="Pré-visualização" 
                    className="h-32 w-32 object-cover rounded-sm"
                  />
                </div>
              </div>
            )}
          </div>
          <div className="flex justify-end space-x-3 mt-6 pt-4 border-t border-gray-200 dark:border-gray-700">
            <Button color="light" onClick={handleCloseModal} sizing="sm" className="rounded-sm">
              Cancelar
            </Button>
            {modalMode !== 'view' && (
              <Button color="blue" onClick={() => {
                if (selectedEPI) {
                  if (modalMode === 'create') {
                    const newEPI = {
                      ...selectedEPI,
                      id: Math.random().toString(36).substr(2, 9)
                    };
                    setFilteredEPIs([...filteredEPIs, newEPI]);
                  } else {
                    setFilteredEPIs(filteredEPIs.map(epi => 
                      epi.id === selectedEPI.id ? selectedEPI : epi
                    ));
                  }
                  handleCloseModal();
                }
              }} sizing="sm" className="rounded-sm">
                {modalMode === 'create' ? 'Cadastrar' : 'Salvar'}
              </Button>
            )}
          </div>
        </div>
      </Modal>
    </div>
  );
};

export default CatalogoEPIs;